.inner-list {
  @include list-reset;
  padding: 25px 18px 46px;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.inner-list__item--apartments {
  background-image: url("../../img/index/house-icon.svg");
  background-repeat: no-repeat;
}

.inner-list__item--food {
  background-image: url("../../img/index/food-icon.svg");
  background-repeat: no-repeat;
}

.inner-list__item--souvenirs {
  background-image: url("../../img/index/gift-icon.svg");
  background-repeat: no-repeat;
}

.inner-list__headline {
  line-height: 26px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 21px;
  }
}

.inner-list__description {
  margin: 0;
  font-size: 14px;
  text-transform: uppercase;
  color: $description-text;
}
